* {
  margin: 0;
  padding: 0;
}

body {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.main_container {
  flex-direction: column;
}

.container,
.main_container {
  align-items: center;
  display: flex;
}

.main {
  height: 500px;
  width: 500px;
}

.box {
  background-color: #83a21a;
  height: 50px;
  position: relative;
  width: 50px;
}

.vertical {
  height: 500px;
  width: 60px;
}

.horizontal,
.vertical {
  align-items: center;
  background-color: #9cfdfd;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.horizontal {
  height: 60px;
  width: 500px;
}

